@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
*{
  margin: 0;
  font-family: "Quicksand";
  color: #333;
  background: white;
}
.navbar{
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 700px;
  margin: 0 auto;
  border-bottom:1px solid #f2f2f2;
}
.navbar h1{
  color: #f1356d;
}

.navbar .links{
  margin-left: auto;
}

.navbar a{
  margin-left: 17px;
   text-decoration: none;
   padding: 10px; 
}

.navbar a:hover{
  color: #f1356d ;
}

.content{
  max-width: 700px;
  margin: 40px auto;
  padding: 20px;
}

.preview{
  padding: 10px 17px;
  margin:20px 0;
  border-bottom:1px solid #fafafa;
  
}

.preview:hover{
 box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}

.preview h1{
  font-size: 27px;
  color: #f1356d;
  margin-bottom: 8px;
}

.preview a{
  text-decoration: none;
}
.blod-detail h1{
  font-size: 20px;
  color:#f1356d;
  margin-bottom: 10px;
}
.blog-detail div{
  margin:20px 0;
}
.blog-detail button{
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

.create {
  max-width: 400px;
  margin:0 auto;
  text-align:center;
}

.create label{
  text-align:left;
  display: block;
}
.create h1{
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}
.create input,.create textarea,.create select{
  width: 100%;
  padding: 6px 10px;
  margin:10px 0;
  border:1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: #f1356d;
  color:#fff;
  border:0;
  padding:8px;
  border-radius: 8px;
  cursor: pointer;
}

.not-found h1{
  color: #f1356d;
  font-size: 70px;
}
.not-found p{
  font-size: large;
}
.not-found a{
  text-decoration: none;
  color: #f1356d;
}
.not-found{
  text-align: center;
  padding: 10px;
}
.not-found a:hover{
  color: #333;
}

.flex{
  display: flex;
  flex-direction: column;
}
.flex input{
  padding: 10px;
  margin: 7px;
  max-width: 300px;
  border: none;
  
}

.login p{
  text-align: center;
  color: #f1356d;
  padding-left: 10px;
  padding-top: 10px;
  margin-left: -340px;
  cursor: pointer;
}
.login{
  cursor: pointer;
}
.button{
  border: orangered;
  color: white; 
  cursor: pointer;
  background-color: #f1356d;
  border-radius: 8px;
  /* background: none; */
}


